body {
  font-family: Arial, sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  margin: 0;
  background-color: #f7f7f7;
}

#chatbox {
  width: 80%;
  max-width: 600px;
  height: 400px;
  border: 1px solid #ccc;
  border-radius: 5px;
  padding: 10px;
  overflow-y: auto;
  background-color: white;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

button {
  padding: 10px 20px;
  border: none;
  background-color: #007bff;
  color: white;
  cursor: pointer;
  border-radius: 5px;
}

button:hover {
  background-color: #0056b3;
}

.message {
  padding: 5px;
  margin: 5px 0;
}

.user-message {
  background-color: #e1f7d5;
  align-self: flex-end;
}

.bot-message {
  background-color: #f0f0f0;
  align-self: flex-start;
}